	module.exports = {"data":{"markdownRemark":{"html":"<h2>Adding and Subtracting Time</h2>\n<p>There are a few basic rules that guide addition and subtraction of time.</p>\n<p>Here they are:</p>\n<p>60 seconds make 1 minute\n60 minutes make 1 hour\n1440 minutes make 1 day\n24 hours make 1 day\n7 days make 1 week\n52 weeks make 1 year\n365 days make 1 year\n366 days make 1 leap year\n12 calendar months or 13 lunar months make 1 year</p>\n<p>For example, subtracting 1 hour 15 minutes 30 seconds from 4 hours would result in the following</p>\n<p>Line 1: 4 00 00\nLine 2: 1 15 30\n--------\n2 44 30\n--------</p>\n<p>On Line 1 above, (subtraction and addition) will start from the right most numbers (in this case 00). Deducting 30 from 0 is normally impossible, so what we would do is go the next set of numbers (00) to see if we can borrow. In this case, it’s impossible so we go to the next number (leftmost which in this case would be 1 hour- remember 60 minutes make 1 hour).</p>\n<p>This is what it would like at this point</p>\n<p>3 60 00\n1 15 30</p>\n<p>Next we need to borrow 1 minute from 60, resulting in what we have below. Remember however that 60 seconds make 1 minute. Reducing 60 by 1 unit results in 59, so we have:</p>\n<p>3 59 60\n1 15 30</p>\n<p>By now we can then make our deductions</p>\n<p>60 minus 30 is 30\n59 minus 15 is 44\n3 minus 1 is 2</p>\n<p>Hence subtracting 1 hour 15 minutes 30 seconds from 4 hours would result in 2 hours 44 minutes 30 seconds.</p>\n<p>This playful youtube video for kids might also be useful as it goes down to the very basics.\n<a href=\"https://www.youtube.com/watch?v=HrxZWNu72WI\">https://www.youtube.com/watch?v=HrxZWNu72WI</a></p>\n<p>Let us see an addition example by adding 3 hours 45 minutes 20 seconds to 2 hours 20 minutes 50 seconds</p>\n<p>H M  S\n3 45 20\n2 20 50</p>\n<p>20 seconds plus 50 seconds is 70 seconds. Recall 60 seconds makes 1 minute. 70 seconds therefore will be 1 minute 10 seconds\n45 minutes plus 20 minutes is 65 minutes. Recall 60 minutes is 1 hour. 65 minutes therefore will be 1 hour 5 minutes\n3 plus 2 is 5 (hours)</p>\n<p>we then need to do the additions.</p>\n<p>5 hours + 1 hour 5 minutes + 1 minute 10 seconds would be 6 hours, 6 minutes and 10 seconds.</p>","fields":{"slug":"/mathematics/adding-and-subtracting-time/"},"frontmatter":{"title":"Adding and Subtracting Time"}}},"pathContext":{"slug":"/mathematics/adding-and-subtracting-time/"}}

